.sider {
  min-height: 100vh; }

.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color .3s;
  transition: color .3s; }
  .trigger:hover {
    color: #1890ff; }

#title {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center; }
  #title .App-logo {
    height: 75%;
    margin-left: 9px;
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear; }
  #title #title-name {
    color: #fff;
    margin-top: 5%; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.custom-filter-dropdown {
  padding: 8px;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15); }

.file {
  opacity: 0;
  position: absolute;
  z-index: 10;
  width: 4vh;
  height: 18px; }


.loading-baba {
  text-align: center;

}

.loading-baba .loading {
  margin-top: 30vh;
}
